	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/2/whos-being-dishonest-with-storage-space/index.md absPath of file >>> MarkdownRemark","html":"<p>Good reporting from Ed Bott, when comparing the Surface Pro to a MacBook Air the amount of free space is about the same. The problem is that people will naturally compare the Surface Pro with an iPad.</p>\n<p>Thought what Bott fails to mention is you can delete the recovery partition on a MacBook Air and still use <a href=\"http://support.apple.com/kb/HT4718\">Internet Recovery</a> to restore OS X. On the Surface Pro, if you want to recover the space used by the recovery partition you have to create an external USB recovery drive that you’ll almost certainly lose.</p>","frontmatter":{"title":"Who's being dishonest with storage space?","date":"February 08, 2013","passthroughUrl":"http://www.zdnet.com/surface-pro-versus-macbook-air-whos-being-dishonest-with-storage-space-7000011009/"}}},"pathContext":{"slug":"/blog/2013/2/whos-being-dishonest-with-storage-space/","previous":{"fields":{"slug":"/blog/2013/2/why-did-dell-buy-dell/"},"frontmatter":{"title":"Why did Dell buy Dell","passthroughUrl":"http://gigaom.com/2013/02/05/welcome-back-michael-dell-its-time-to-reclaim-your-name/","type":null}},"next":{"fields":{"slug":"/blog/2013/2/android-fragmentation-is-a-security-flaw/"},"frontmatter":{"title":"Android fragmentation is a security flaw","passthroughUrl":"http://www.washingtonpost.com/business/technology/android-phones-vulnerable-to-hackers/2013/02/01/f3248922-6723-11e2-9e1b-07db1d2ccd5b_story.html","type":null}}}}

